<template>
    <div>
        <van-card v-for="(k,index) in restaurant" :key="index" price="12.00" :desc="k.restaurant.activities[0].description" :title="k.restaurant.name" :thumb="k.restaurant.image_path" />
    </div>
</template>
<script>
import Vue from 'vue'

import { Card } from 'vant';
Vue.use(Card);
export default {
    data() {
        return {
            restaurant: [],
        }
    },
    async created() {
        let godList = await this.$axios(
            "https://www.easy-mock.com/mock/5d402d6799acfe0359e0189d/main/goods")
        // console.log(godList.data.items);
        this.restaurant = godList.data.items;


    },
}
</script>

