<template>
    <div>
        <van-swipe indicator-color="white">
            <van-swipe-item>
                <van-grid :column-num="5">
                    <van-grid-item v-for="(k,index) in kingkongListComputed1" :key="index" :icon="k.image_hash" :text="k.name" />
                </van-grid>
            </van-swipe-item>

            <van-swipe-item>
                <van-grid :column-num="5">
                    <van-grid-item v-for="(k,index) in kingkongListComputed2" :key="index" :icon="k.image_hash" :text="k.name" />
                </van-grid>
            </van-swipe-item>

        </van-swipe>

        <van-divider :style="{ color: '#666', borderColor: '#ccc', padding: '0 16px' }">
            推荐商家
        </van-divider>
        <van-dropdown-menu>
            <van-dropdown-item v-model="value1" :options="menu|handleMenu" />
            <van-dropdown-item v-model="value2" :options="menu|handleMenu" />
            <van-dropdown-item v-model="value3" :options="menu|handleMenu" />
            <van-dropdown-item v-model="value4" :options="menu|handleMenu" />
        </van-dropdown-menu>
    </div>
</template>

<script>
import { Swipe, SwipeItem } from 'vant';

Vue.use(Swipe).use(SwipeItem);


import Vue from 'vue'
import { Grid, GridItem } from 'vant';
Vue.use(Grid).use(GridItem);
//分割线
import { Divider } from 'vant';
Vue.use(Divider);
//下拉菜单
import { DropdownMenu, DropdownItem } from 'vant';
Vue.use(DropdownMenu).use(DropdownItem);
export default {
    data() {
        return {
            kingkongList: [],
            value1: 0,
            value2: 9,
            value3: 10,
            value4: 11,
            menu: [],

        }
    },
    async created() {
        let listnav = await this.$axios(
            "https://www.easy-mock.com/mock/5d40113ccf75387cbda8c48b/example/listnav")
        // console.log(listnav.data.data);
        this.kingkongList = listnav.data.data;
        // 获取下拉菜单
        let bar = await this.$axios(
            "https://www.easy-mock.com/mock/5d40113ccf75387cbda8c48b/example/bar"
        );
        // console.log(bar.data.inside_sort_filter);
        this.menu = bar.data.inside_sort_filter;

    },
    computed: {
        kingkongListComputed1() {
            // 切割成八个宫格
            return this.kingkongList.slice(0, 10);
        },
        kingkongListComputed2() {
            // 切割成四个宫格
            return this.kingkongList.slice(10);
        }
    },
    filters: {
        handleMenu(menu) {
            let newMenus = [];
            menu.map((item, index) => {
                //   往对象里面新增一个text属性值
                item.text = item.name;
                item.value = index;
                // 构造一个新的数组
                newMenus.push(item);
            })
            return newMenus;
        }
    }
}
</script>
<style>
.van-grid-item__content {
  padding: 10px 5px;
}
.van-ellipsis {
  font-size: 12px;
  line-height: 12px;
}
.van-divider {
  margin-bottom: 0;
}
</style>
